import React from "react";

import Contacts from "./contacts/contacts";

import styles from './landing.module.css';

const Landing = () => {
    return (
        <div className={`row flex ${styles.alignContentCenterFull}`} style={{ minHeight: '90vh', paddingTop: '1em' }}>
            <div className="col-md-11 col-sm-12" style={{ textAlign: 'justify' }}>
                <div className={`col-lg-6 col-md-12 ${styles.noPadding}`}>
                    <h1 style={{textAlign: 'left'}}>Luca Castellaro</h1>
                    <p>Software engineer at <a href="https://www.txtgroup.com">TXT E-Solutions</a></p>
                    <Contacts />
                </div>
            </div>
        </div>
    );
}

export default Landing;