import React from 'react';
import EN_CV from '../../../../static/EN_CV.pdf';

import { Linkedin, GitHub, FileText } from 'react-feather';

const contacts = [
    { icon: <Linkedin />, title: 'LinkedIn', action: () => OpenLink('https://it.linkedin.com/in/lucacastellaro') },
    { icon: <GitHub />, title: 'Github', action: () => OpenLink('https://github.com/LucaCastellaro') },
    { icon: <FileText />, title: 'CV download', action: () => OpenLinkInNewTab(EN_CV) }
];

const OpenLink = (link) => {
    window.open(link, "_self");
}

const OpenLinkInNewTab = (link) => {
    window.open(link, "_blank");
}

const Contacts = () => {
    return (
        <div>
            {
                contacts.map((item, index) => {
                    return (
                        <button className="btn btn-light btn-lg"
                            {...{
                                key: index,
                                title: item.title,
                                onClick: () => item.action(),
                                style: {
                                    marginRight: '1em'
                                }
                            }}>
                            {item.icon}
                        </button>
                    );
                })
            }
        </div>
    );
}

export default Contacts;