import React from 'react';

const currentYear = new Date().getFullYear();

const Footer = () => {
    return (
        <div className="row align-content-center-horizontal">
            <div className="col-md-11 col-12">
                <hr />
            </div>
            <div className="col-md-11 col-12 vertical-breath flex align-content-center-horizontal">
                <p className="details">Luca Castellaro, 2014 - {currentYear}</p>
            </div>
        </div>
    );
}

export default Footer;